import React from 'react';
import './Coin.css';

const Coin = ({
	current_price,
	symbol,
	image,
	price_change_percentage_24h,
	ath,
	ath_date,
}) => {
	return (
		<div className='wrapper'>
			<img src={image} alt={symbol} className='coin_img' />
			<h2 className='symbol'>{symbol.toUpperCase()}</h2>
			<div className='price'>
				<span className='curr_price'>{current_price.toLocaleString()}$</span>
				{price_change_percentage_24h < 0 ? (
					<span className='price_change red'>
						{price_change_percentage_24h.toFixed(2)}%
					</span>
				) : (
					<span className='price_change green'>
						+{price_change_percentage_24h.toFixed(2)}%
					</span>
				)}
			</div>
			<div className='ath'>
				<span className='curr_ath'>ATH {ath.toLocaleString()}$</span>
				<span className='ath_date'>
					{new Date(ath_date).toDateString().slice(4)}
				</span>
			</div>
		</div>
	);
};

export default Coin;
