import './App.css';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import Coin from './components/Coin';

const App = () => {
	const api =
		'https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&ids=bitcoin%2C%20ethereum%2C%20binancecoin%2C%20ripple%2C%20polkadot%2C%20cardano%2C%20solana%2C%20litecoin%2C%20cosmos%2C%20pancakeswap-token&order=market_cap_desc&per_page=20&page=1&sparkline=false';
	const [crypto, setCrypto] = useState([]);

	const fetchMyAPI = async () => {
		try {
			const { data } = await axios.get(api);
			setCrypto(data);
		} catch (err) {
			console.error(err);
		}
	};

	useEffect(() => {
		fetchMyAPI();
	}, []);

	return (
		<div className='App'>
			{crypto.map(coin => (
				<Coin
					key={coin.id}
					current_price={coin.current_price}
					symbol={coin.symbol}
					image={coin.image}
					price_change_percentage_24h={coin.price_change_percentage_24h}
					ath={coin.ath}
					ath_date={coin.ath_date}
				/>
			))}
		</div>
	);
};

export default App;
